import axios from 'axios';
import React, {useState, useEffect} from 'react'; 
import { useHistory, useParams } from 'react-router-dom';
import styled from 'styled-components';
import './Detail.css';

let 박스 = styled.div`
    padding:20px;
`;
let 제목 = styled.h4`
    font-size: 25px;
    color:${ props=>props.색상 }
`;
let 제목2 = styled.h4`
    font-size: 25px;
    color:red;
`;
{/*
class Detail2 extends React.Component {
    componentDidMount() {

    }
    componentWillUnmount() {

    }

}
*/ }


function Detail(props){

    let [alert, alert변경] = useState(true);
    let [inputData, inputData변경] = useState('');


    // 실행할때, 업데이트 될떄도 실행
    useEffect(() =>{

        // detail compoent 실행될떄, 업데이트 될때
        axios.get();

        //console.log(11111);
        //let 타이머 = setTimeout(()=>{      modal변경(true)        }, 2000)

        let 타이머 = setTimeout(()=>{      alert변경(false)        }, 2000);
        //console.log('234');
        
        // detail component가 사라질때 타이머 없어짐, 예를 들어, 2초가 되기전에 페이지를 이동시에 문제가 생길수 있음
        return () => { clearTimeout(타이머)}

        
        // unmount시 실행
        //return function 어쩌구() { 실행할코드~~~}
        //return () => { 실행할코드~~~}
    },[]);

    
    useEffect(() =>{        
        axios.get();        
    },[]);

    // alert가 update될때만 실행
    // [] _가 변경될때만, 로드될떄 1번만 실행
    // useEffect 여러개 사용 가능. 순서대로 실행

    //let [modal, modal변경] = useState(false);

    


    let { id } = useParams();   
     
    let 찾은상품 = props.shoes.find(function(상품){
        return 상품.id == id
      });
    // let 찾은상품 = props.shoes.find(x => x.id == id);
    let history = useHistory();

    return (
        <div className="container">
            
            <박스>
            {/*
                <제목 색상={'red'}>Detail</제목>            
                <제목 색상="blue">Detail</제목>
            */ }
                <제목 className="red">Detail</제목>
            </박스>
            
            {inputData}
            <input onChange={ (e) => { inputData변경(e.target.value)  } }/>
            {
                alert===true 
                ? (<div className="my-alert2" id="my-alert2">
                    <p>재고가 얼마 안남았습니다.</p>
                    </div>)
                : null

            }
            
            {/*
            {
             modal === false
            ? <div className="my-alert2" id="my-alert2"><p>재고가 얼마 안남았습니다.</p></div>
            : null
            }
            */}
            {/*
            <div className="my-alert2" id="my-alert2">
                <p>재고가 얼마 안남았습니다.</p>
            </div>
            */ }
            <div className="row">
                <div className="col-md-6">
                    <img src={"https://codingapple1.github.io/shop/shoes"+(찾은상품.id+1)+".jpg"} width="100%" />
                </div>
                <div className="col-md-6 mt-4">
                    <h4 className="pt-5">{찾은상품.title}</h4>
                    <p>{찾은상품.content}</p>
                    <p>{찾은상품.price}원</p>
                    {props.재고[찾은상품.id]}

                    <Info  재고={props.재고} 상품id={찾은상품.id}/>




                    <button className="btn btn-danger"
                    onClick={()=>{
                        //
                        var newArray = [...props.재고];
                        //newArray[0] = '여자코트 추천22';
                        newArray[찾은상품.id] = newArray[찾은상품.id]-1;
                        props.재고변경(newArray);
                    }}
                    >주문하기</button> &nbsp;
                    <button className="btn btn-danger" onClick={()=>{ 
                        history.goBack();
                    }}>뒤로가기</button>   &nbsp;
                    <button className="btn btn-danger" onClick={()=>{ 
                        history.push('/asdf');
                    }}>/가기</button> 
                </div>
            </div>
        </div> 
    )
}

function Info(props) {
    return (
        <p>재고 :  {props.재고[props.상품id]} </p>
    );
}

export default Detail;